.carousel-indicators {
  left: auto !important;
  bottom: 10% !important; }

.carousel-indicators li {
  height: 8px !important;
  width: 8px !important;
  border-radius: 50%;
  margin-right: 8px;
  background: #fff; }

.carousel-indicators li.active {
  background: #333; }

.carousel-control-prev {
  right: 160px !important;
  left: auto !important; }

.carousel-control-next {
  right: 100px !important; }

.carousel-control-prev, .carousel-control-next {
  height: 20px !important;
  width: 20px !important;
  bottom: calc(10% + 10px) !important;
  top: auto !important;
  opacity: 1 !important; }

.carousel-caption {
  bottom: auto !important;
  top: 20% !important;
  right: auto !important;
  left: 10% !important;
  text-align: left;
}

.carousel-caption h5 {
  font-size: 60px;
  line-height: 1.2;
  font-weight: 700;
  color: #FFF
 }

.carousel-caption p {
  margin-top: 20px;
  margin-bottom: 50px;
  font-size: 16px;
  color: #FFF
}

.carousel-caption button a {
  position: relative; }

.carousel-caption button img {
  position: absolute;
  top: 25px;
  left: 23px; }

.carousel-caption button img:last-of-type {
  opacity: 0; }

.carousel-caption a {
  display: inline-block;
  height: 80px;
  width: 80px;
  border: 2px solid #fff;
  border-radius: 50%;
  text-align: center;
  line-height: 70px;
  top: 3px; }

.carousel-caption button:hover img:first-of-type {
  opacity: 0; }

.carousel-caption button:hover img:last-of-type {
  opacity: 1; }

.carousel-caption button {
  height: 90px;
  border-radius: 50%; }

.carousel-caption button:hover {
  -webkit-animation: hoverAnimation .3s ease-in-out;
  animation: hoverAnimation .3s ease-in-out;
  border: none; }

.carousel-caption button:hover a {
  background: #fff; }

@-webkit-keyframes hoverAnimation {
  0% {
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
    background: rgba(255, 255, 255, 0.2); }
  25% {
    -webkit-transform: scale(1.15);
    transform: scale(1.15);
    background: rgba(255, 255, 255, 0.4); }
  50% {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
    background: rgba(255, 255, 255, 0.6); }
  75% {
    -webkit-transform: scale(1.05);
    transform: scale(1.05);
    background: rgba(255, 255, 255, 0.4); }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
    background: rgba(255, 255, 255, 0.2);
    opacity: 1; } }

@keyframes hoverAnimation {
  0% {
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
    background: rgba(255, 255, 255, 0.2); }
  25% {
    -webkit-transform: scale(1.15);
    transform: scale(1.15);
    background: rgba(255, 255, 255, 0.4); }
  50% {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
    background: rgba(255, 255, 255, 0.6); }
  75% {
    -webkit-transform: scale(1.05);
    transform: scale(1.05);
    background: rgba(255, 255, 255, 0.4); }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
    background: rgba(255, 255, 255, 0.2);
    opacity: 1; } }

.carousel-control-prev img, .carousel-control-next img {
  position: absolute;
  height: 28px;
  width: 17px; }

.carousel-control-prev:hover img:last-of-type, .carousel-control-next:hover img:last-of-type {
  opacity: 1; }

.carousel-control-prev:hover img:first-of-type, .carousel-control-next:hover img:first-of-type {
  opacity: 0; }

.carousel-control-prev img:last-of-type, .carousel-control-next img:last-of-type {
  opacity: 0; }

.carousel-control-prev span, .carousel-control-next span {
  opacity: 0; }
.carousel-item >img ,
.img-slide,
video {
  height: 100%;
